import { createI18n } from 'vue-i18n'
import EN from "./en"
import ZH from "./zh"
import JP from "./jp"
import KOR from "./kor"
import FRA from "./fra"
import SPA from "./spa"
import VIE from "./vie"
import TH from "./th"
import ARA from "./ara"
import RU from "./ru"
import PT from "./pt"
import DE from "./de"
import IT from "./it"
import EL from "./el"
import NL from "./nl"
import PL from "./pl"
import BUL from "./bul"
import EST from "./est"
import DAN from "./dan"
import FIN from "./fin"
import CS from "./cs"
import ROM from "./rom"
import SLO from "./slo"
import SWE from "./swe"
import HU from "./hu"


const messages = {
    en: {...EN },
    zh: {...ZH },
    vie: { ...VIE },
    jp: { ...JP },
    kor: { ...KOR },
    fra: { ...FRA },
    spa: { ...SPA },
    th: { ...TH },
    ara: { ...ARA },
    ru: { ...RU },
    pt: { ...PT },
    de: { ...DE },
    it: { ...IT },
    el: { ...EL },
    nl: { ...NL },
    pl: { ...PL },
    bul: { ...BUL },
    est: { ...EST },
    dan: { ...DAN },
    fin: { ...FIN },
    cs: { ...CS },
    rom: { ...ROM },
    slo: { ...SLO },
    swe: { ...SWE },
    hu: { ...HU },
}
const i18n = createI18n({
    // 使用 Composition API 模式，则需要将其设置为false
    legacy: false,
    // 全局注入 $t 函数
    globalInjection: true,
    locale: sessionStorage.getItem('lange') || 'zh',
    messages,
    // 防止使用v-html=“$t(“”)”的xss警告
    warnHtmlMessage: false
})

export default i18n
