// store.js
import { createStore } from 'vuex'
// import axios from 'axios'


export default createStore({
  state: {
    info: '',
    cloud: null,
    isLoad: true,
    languageList: []
  },
  mutations: {
    setInfo(state, info) {
      state.info = info
    },
    setCloud(state, cloud){
      state.cloud = cloud
    },
    setLoad(state, load) {
      state.isLoad = load
    },
    setLanguage(state, language) {
      state.languageList = language
    }
  },
  actions: {
    async fetchInfo({ commit }, language = 'zh') {
      let websiteData = {}
      // 注册微信云函数
      const cloud = new window.cloud.Cloud({
        // 必填，表示是未登录模式
        identityless: true,
        // 资源方 AppID
        resourceAppid: 'wxd2b91739417f7a9a',
        // 资源方环境 ID
        resourceEnv: 'ai-weka-cloud-3gtwcbz8ddfef45a',
      })
      await cloud.init()
      try {
          const href = window.location.href;
          let websiteId = ""
          if(href.indexOf('websiteid') > -1) {
            const arry = href.split('?');
            const arry1 = arry[1].split('&');
            websiteId = arry1[0].split('=')[1]
          }
          if (websiteId) {
            let res = await cloud.callFunction({
              name: 'aiWebsiteFunctions',
              data: {
                method: 'getWebsiteById',
                websiteId: websiteId,
                language: language === 'zh' ? '' : language
              }
            })
            websiteData = res.result.data
          } else {
            // const domain = window.location.host;
            const domain = "jnhmjsggkj.ai-site.cc"
            // url = `http://weka-ai-website-api.ai-site.cc/api/get-website-data?domain=${domain}&language=${ language }`
            // const response = await axios.get(url)
            // if (response?.data.resp_data) {
            //   let data = JSON.parse(response?.data.resp_data)
            //   websiteData = data[0]
            // }
            let res = await cloud.callFunction({
              name: 'aiWebsiteFunctions',
              data: {
                method: 'getWebsiteById',
                domain: domain,
                language: language === 'zh' ? '' : language
              }
            })
            websiteData = res.result.data
          }
          if (websiteData && JSON.stringify(websiteData) !== "{}") {
            websiteData.news = websiteData.company_news.concat(websiteData.industry_news)
            document.title = websiteData.company_name
            let fileList = []
            // 修改转换logo的地址
            if(websiteData.logo.logo_url && websiteData.logo.logo_url.startsWith('cloud://')) {
              fileList.push({
                file: websiteData.logo.logo_url,
                name: 'logo_url'
              })
            }
            // 转化ico的地址
            if (websiteData.logo.ico_url && websiteData.logo.ico_url.startsWith('cloud://')) {
              fileList.push({
                file: websiteData.logo.ico_url,
                name: 'ico_url'
              })
            }
            // 转化banner的地址
            if (websiteData.banners.banner_urls) {
              websiteData.banners.banner_urls.forEach((el, index) => {
                if (el.url && el.url.startsWith('cloud://')) {
                  fileList.push({
                    file: el.url,
                    index,
                    name: 'banner_urls'
                  })
                }
              })
            }
            // 转化企业文化
            if(websiteData.culture.culture_images && websiteData.culture.culture_images.startsWith('cloud://')) {
              fileList.push({
                file: websiteData.culture.culture_images,
                name: 'culture'
              })
            }
            // 转化新闻
            if (websiteData.news) {
              websiteData.news.forEach((el, index) => {
                if (el.image_url && el.image_url.startsWith('cloud://')) {
                  fileList.push({
                    file: el.image_url,
                    index,
                    name: 'news'
                  })
                }
              })
            }
            // 转化产品
            if (websiteData.products) {
              websiteData.products[0].items.forEach((el, index) => {
                if (el.product_image_url && el.product_image_url.startsWith('cloud://')) {
                  fileList.push({
                    file: el.product_image_url,
                    index,
                    name: 'products'
                  })
                }
              })
            }
            // 转化案例
            if (websiteData.case) {
              websiteData.case[0].items.forEach((el, index) => {
                if (el.product_image_url && el.product_image_url.startsWith('cloud://')) {
                  fileList.push({
                    file: el.product_image_url,
                    index,
                    name: 'case'
                  })
                }
              })
            }
            if (fileList.length > 0) {
              let res = await cloud.callFunction({
                name: 'pageFunctions',
                data: {
                    method:'getTempFileUrl',
                    fileList: fileList.map(el => el.file)
                }
              })
              if(res.result.code === 200) {
                fileList.forEach((el,index) => {
                  if (el.name === "banner_urls") {
                    websiteData.banners.banner_urls[el.index].url = res.result.data[index].tempFileURL
                  } else if(el.name === "culture") {
                    websiteData.culture.culture_images = res.result.data[index].tempFileURL
                  } else if(el.name === "news") {
                    websiteData.news[el.index].image_url = res.result.data[index].tempFileURL
                  } else if(el.name === "products") {
                    websiteData.products[0].items[el.index].product_image_url = res.result.data[index].tempFileURL
                  } else if(el.name === "logo_url") {
                    websiteData.logo.logo_url = res.result.data[index].tempFileURL
                  }else if(el.name === "ico_url"){
                    websiteData.logo.ico_url = res.result.data[index].tempFileURL
                  } else if(el.name === "case") {
                    websiteData.case[0].items[el.index].product_image_url = res.result.data[index].tempFileURL
                  }
                })
                if (websiteData.logo.ico_url) {
                  document.getElementById('link').href = websiteData.logo.ico_url
                }
              }
            }
          }
          if (websiteData.is_language && websiteData.language !== language) {
            let res = await cloud.callFunction({
              name: 'aiWebsiteFunctions',
              data: {
                method: 'languageList',
                websiteId: websiteData._id
              }
            })
            if (res.result.code == 200) {
              if (res.result.data.length === 1 && res.result.data[0].is_use && websiteData.language !== language) {
                this.dispatch('fetchInfo', res.result.data[0].language)
                return;
              }
              commit('setLanguage', res.result.data)
            }
          }
        commit('setInfo', websiteData)
        commit('setCloud', cloud)
        commit('setLoad', false)
      } catch (error) {
        console.error(error)
      }
    },
  }
});