<template>
    <div class="head">
        <div class="center">
            <div>
                <img :src="info.logo.logo_url" class="logo"/>
            </div>
            <div class="m-l-14">
                <div class="company-name">{{ info.company_name }}</div>
                <div class="company-en">{{ info.company_english }}</div>
            </div>
            <el-dropdown class="m-l-20" v-if="info.is_language">
                <span class="el-dropdown-link">
                {{ useLan }}
                <el-icon class="el-icon--right">
                    <arrow-down />
                </el-icon>
                </span>
                <template #dropdown>
                <el-dropdown-menu>
                    <template v-for="item in dropdown" :key="item.language">
                        <el-dropdown-item v-if="item.is_use" @click="changeLang(item.language)">
                            {{ languageName(item.language) }}
                        </el-dropdown-item>
                    </template>
                </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
        <ul class="ul">
            <li @click="navigator('/')">
                {{ $t('home') }}
                <div class="block" :class="{ 'check-block': check === '/' }"></div>
            </li>
            <li @click="navigator('/aboutUs')">
                {{ $t('aboutUs') }}
                <div class="block" :class="{ 'check-block': check === '/aboutUs' }"></div>
            </li>
            <li @click="navigator('/news')">
                {{ $t('pressCenter') }}
                <div class="block" :class="{ 'check-block': check === '/news' }"></div>
            </li>
            <li @click="navigator('/product')">
                {{ $t('productCenter') }}
                <div class="block" :class="{ 'check-block': check === '/product' }"></div>
            </li>
            <li @click="navigator('/case')">
                {{ $t('case') }}
                <div class="block" :class="{ 'check-block': check === '/case' }"></div>
            </li>
            <li @click="navigator('/job')">
                {{ $t('recruitmentInformation') }}
                <div class="block" :class="{ 'check-block': check === '/job' }"></div>
            </li>
            <li @click="navigator('/contactUs')">
                {{ $t('contactUs') }}
                <div class="block" :class="{ 'check-block': check === '/contactUs' }"></div>
            </li>
        </ul>
    </div>
</template>
<script setup>
    import { computed, onMounted, reactive, ref } from "vue";
    import { useRoute, useRouter } from "vue-router";
    import { useStore } from 'vuex';
    import { ArrowDown } from '@element-plus/icons-vue'
    import { useI18n } from "vue-i18n";
    const { locale } = useI18n();

    const route = useRoute();
    const router = useRouter();
    let check = computed(() => route.path);
    const store = useStore();
    const info = computed(() => store.state.info);
    const language = [
        { name: '中文', code: 'zh' },
        { name: 'English', code: 'en' },
        { name: '日本語', code: 'jp' },
        { name: '한국어', code: 'kor' },
        { name: 'Français', code: 'fra' },
        { name: 'Español', code: 'spa' },
        { name: 'ภาษาไทย', code: 'th' },
        { name: 'بالعربية', code: 'ara' },
        { name: 'Русский язык', code: 'ru' },
        { name: 'Português', code: 'pt' },
        { name: 'Deutsch', code: 'de' },
        { name: 'Italiano', code: 'it' },
        { name: 'Ελληνική γλώσσα', code: 'el' },
        { name: 'Nederlands', code: 'nl' },
        { name: 'Język polski', code: 'pl' },
        { name: 'Български', code: 'bul' },
        { name: 'Eesti keel', code: 'est' },
        { name: 'Dansk sprog', code: 'dan' },
        { name: 'Suomen kieli', code: 'fin' },
        { name: 'Čeština', code: 'cs' },
        { name: 'română', code: 'rom' },
        { name: 'slovenski jezik', code: 'slo' },
        { name: 'Svenska språket', code: 'swe' },
        { name: 'Magyar nyelv', code: 'hu' },
        { name: 'Tiếng Việt', code: 'vie' }
    ];
    let dropdown = computed(() => store.state.languageList);
    let useLan = ref('中文');
    const navigator = (path) => {
        router.push(path)
    }

    const changeLang = (value, name) => {
        if (value !== useLan.value) {
            sessionStorage.setItem('lange', value);
            locale.value = value;
            useLan.value = languageName(value);
            store.dispatch('fetchInfo', value);
        }
        
    };
    onMounted(() => {
        const lan = sessionStorage.getItem('lange');
        if(lan) {
            useLan.value = languageName(lan);
        }
    })
    const languageName = (value) => {
        if (value) {
            return language.find(el => el.code === value)?.name;
        }
        return ''
    }

</script>
